import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 
const state = {
    // token
    token: '',
    // 
    userInfo: {},
    // 
    count: 0
}
// mutations 
const mutations = {
    incerment (state, value) {
      state.count += value
    }
}
// vuex  store()
const store = new Vuex.Store({
    // state: state,
    // mutations; mutations
    // es6    
    state,
    mutations
})
export default store



