<template>
  <div class="editor">
    <div ref="toolbar" class="toolbar"></div>
    <div ref="editor" class="text"></div>
  </div>
</template>

<script>
  import E from 'wangeditor'
  export default {
    name: 'editoritem',
    components: { E },
    props: {
      value: {
        type: String,
        default: ''
      },
      isClear: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        editor: null,
        info_: null
      }
    },
    model: {
      prop: 'value',
      event: 'change'
    },
    watch: {
      isClear(val) {
        // 
        if (val) {
          this.editor.txt.clear()
          this.info_ = null
        }
      },
      value: function(value) {
        if (value !== this.editor.txt.html()) {
          this.editor.txt.html(this.value)
        }
      }
      //valuevalue
    },
    mounted() {
      var editor = new E('#editor') //editor
      this.seteditor()
      editor.txt.html(this.value)
    },
    methods: {
      seteditor() {
        this.editor = new E(this.$refs.toolbar, this.$refs.editor)
        this.editor.config.height = 800;
        // this.editor.config.uploadImgServer = 'http://10.103.0.245:8888/questions/add' 
        this.editor.config.uploadImgShowBase64 = true //  false 
        this.editor.config.showLinkImg = false; //
        this.editor.config.uploadFileName = 'qfile'
        this.editor.config.uploadImgTimeout = 50000000;
        this.editor.config.uploadImgMaxLength = 5 //  5 

        // 
        this.editor.config.menus = [
          'head', // 
          'bold', // 
          'fontSize', // 
          'fontName', // 
          // 'italic', // 
          // 'underline', // 
          // 'strikeThrough', // 
          'foreColor', // 
          // 'backColor', // 
          // 'link', // 
          'list', // 
          'justify', // 
          // 'quote', // 
          'image', // 
          'table', // 
          'code', //
          'undo', // 
          'redo', // 
        ]
        //
        this.editor.config.uploadImgHooks = {
          // fail: (xhr, editor, result) => {// 
          //   console.log('',result)
          // },
          // success: (xhr, editor, result) => {// 
          //   console.log('',result)
          //   // xhr  XMLHttpRequst editor result 
          // },
          // timeout: (xhr, editor) => {// 
          //   console.log('aaaa')
          // },
          // error: (xhr, editor,result) => {// 
          //   console.log('',result)
          // },
          customInsert: (insertImgFn, result, editor) => {// 
            
            //result 
            // console.log('',result.data1)
            //insertImg()
             //
            // for (let i = 0; i < 1; i++) {
              // console.log(result)
            // let id = result.data.fileId;
            // let a = result.data.fileName.split('.')[1];
            // let str = id + '/' + a;
            // let url = 'fileId=' + str;
            // let url = "http://otp.cdinfotech.top"+result.url
            // var url1 = result.data1
            // var  url2 = result.data2
            // insertImgFn(url1)
            // }
          }
        }
        this.editor.config.onchange = (html) => { //  onchange  vue 
          this.info_ = html // 
          this.$emit('change',this.info_) // 
        }
        // 
        this.editor.create()
      }
    }
  }
</script>

<style scoped>
/* css>>>scssscss>>>, */
.editor {
    width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 0;
    font-size: .16rem;
}
.toolbar {
    border: 1px solid #ccc;
    height:.4rem;
    border-bottom: 0;
}
.text {
    border: 1px solid #ccc;
    height: 4rem;
}
</style>


